body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

header {
  background-color: #3d313b;
  width: 100%;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  padding: 10px;
  color: #d95449;
  box-sizing: border-box;
  -webkit-animation: header-drop-in 2s;
  /* Safari 4+ */
  /* Fx 5+ */
  /* Opera 12+ */
  animation: header-drop-in 2s;
  /* IE 10+, Fx 29+ */ }
  header h1 {
    margin: 0; }
  header ul {
    display: -webkit-flex;
    display: flex;
    list-style: none;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    padding: 0;
    margin: 0;
    -webkit-align-items: center;
            align-items: center; }
    header ul li {
      margin: 10px;
      cursor: pointer; }

@-webkit-keyframes header-drop-in {
  0% {
    top: 120vh; }
  100% {
    top: 0; } }

@keyframes header-drop-in {
  0% {
    top: 120vh; }
  100% {
    top: 0; } }

.about-me {
  background: grey;
  -webkit-flex: 1 1;
          flex: 1 1;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center; }
  .about-me h1 {
    max-width: 600px;
    -webkit-animation: header-slide-in 3s;
    /* Safari 4+ */
    /* Fx 5+ */
    /* Opera 12+ */
    animation: header-slide-in 3s;
    /* IE 10+, Fx 29+ */ }

@-webkit-keyframes header-slide-in {
  0% {
    left: 120vw; }
  66% {
    left: 120vw; }
  100% {
    left: 0; } }

@keyframes header-slide-in {
  0% {
    left: 120vw; }
  66% {
    left: 120vw; }
  100% {
    left: 0; } }

.services {
